import { CartItem, UserState } from "../../common/interfaces";
import { ADD_TO_CART,UPDATE_USER, CLEAR_ALL, REMOVE_FROM_CART, SET_USER, LIKE, UNLIKE, ADD_ORDER, CLEAR_CART, SET_IS_DELIVERED } from "../actions/actions";
import firebase from 'firebase';

const initState:UserState = {
  id:null,
  username:null,
  address:null,
  zipCode:null,
  cart:null,
  genres:[],
  liked:[],
  orders:null,
}

const addToCart = (state:UserState,action:any)=>{
  let updatedCart:CartItem[] = [];
  const cart = state.cart;
  if(cart===undefined || cart===null){
    updatedCart = [{id:action.id,quantity:1,price:action.price,bookInfo:action.bookInfo,discount:action.discount}];
  }else{
    updatedCart = cart.slice();
    const isPresent = updatedCart.findIndex((item)=>(item.id===action.id));
    if(isPresent===-1){
      updatedCart.push({id:action.id,quantity:1,price:action.price,bookInfo:action.bookInfo,discount:action.discount}); 
    }else{
      updatedCart[isPresent].quantity = action.quantity;  
    }
  }
  return {...state,cart:updatedCart};
}

const removeFromCart = (state:UserState,action:any)=>{
  const cart = state.cart;
  if(cart===null || cart===undefined || cart.length===0)return state;
  else{
    const updatedCart = cart.filter(p=>p.id!==action.id);
    return {...state,cart:updatedCart};
  }
}

const updateUser = (state:UserState,action:any)=>{
  console.log(state);
  firebase.database().ref('users/' + state.id).set(state);
  return state;
}

const likeFunc = (state:UserState,action:any)=>{
  console.log(action);
  let liked = state.liked?.slice();
  if(liked === undefined){
    liked = [{id:action.id,quantity:1,price:action.price,bookInfo:action.bookInfo,discount:action.discount}];
  }else
  liked.push({id:action.id,quantity:1,price:action.price,bookInfo:action.bookInfo,discount:action.discount});
  return {...state,liked};
}

const unlikeFunc = (state:UserState,action:any)=>{
  let liked = state.liked?.slice();
  if(liked===undefined)return state;
  liked = liked.filter((item)=>item.id!==action.id);
  return {...state,liked};
}

const addOrder = (state:UserState, action:any)=>{
  let orders = state.orders?.slice();
  if(orders===undefined || orders===null)
  orders = [action.order];
  else
  orders.push(action.order);
  return {...state,orders,cart:[]};
}

const setOrder = (state:UserState,action:any)=>{
  let orders = state.orders?.slice();
  if(orders!==undefined){
    orders[action.index].isDelivered = true;
  }
  return {...state,orders};
}

export default (state = initState, action:any) => {
  switch (action.type) {
    case SET_USER:return action.userState;
    case ADD_TO_CART: return addToCart(state,action);
    case REMOVE_FROM_CART: return removeFromCart(state,action);
    case UPDATE_USER: return updateUser(state,action);
    case LIKE: return likeFunc(state,action);
    case UNLIKE: return unlikeFunc(state,action);
    case ADD_ORDER: return addOrder(state,action);
    case SET_IS_DELIVERED: return setOrder(state,action);
    case CLEAR_CART: return {...state,cart:null};
    case CLEAR_ALL:return initState;
    default:
      return state;
  }
};