import {CommonState} from "../../common/interfaces";
import { CLEAR_ALL, CLEAR_ERROR, CLEAR_MESSAGE, LOAD_BOOK_ERROR, SET_ERROR, SET_MESSAGE, TOGGLE_LOADING } from "../actions/actions";

const initState:CommonState = {
  loading:false,
  error:null,
  message:null,
};

const loadBookError=(state:CommonState,action:any)=>({...state,error:{
  code:"Something went wrong while loading book!",
  message:action.error,
}})

export default (state = initState, action:any) => {
  switch (action.type) {
    case TOGGLE_LOADING: return {...state,loading:!state.loading};
    case LOAD_BOOK_ERROR: return loadBookError(state,action);
    case SET_ERROR: return {...state,error:{code:action.code,message:action.message}};
    case CLEAR_ERROR: return {...state,error:null};
    case SET_MESSAGE: return {...state,message:action.message};
    case CLEAR_MESSAGE: return {...state,message:null};
    case CLEAR_ALL:return initState;
    default:
      return state;
  }
};
