import { AuthState } from "../../common/interfaces";
import { CLEAR_ALL, SET_IS_AUTH } from "../actions/actions";

const initState:AuthState = {
  isAuth:false,
};

export default (state = initState, action:any) => {
  switch (action.type) {
    case SET_IS_AUTH: return {...state,isAuth:action.isAuth};
    case CLEAR_ALL:return initState;
    default:
      return state;
  }
};
