import React, { useEffect } from 'react'
import { Card, CardContent, createStyles, makeStyles, Theme, Grid, InputAdornment, IconButton, Button, Chip, MenuItem, TextField, Typography, List ,ListItem, ListItemIcon,ListItemText} from '@material-ui/core'
import {Visibility, VisibilityOff} from '@material-ui/icons';
import { useState } from 'react';
import { signUpMethod } from '../store/actions';
import { useDispatch, useSelector } from 'react-redux';
import { StoreState } from '../common/interfaces';
import { loginMethod } from '../store/actions/auth';
import { app_name, genres } from '../common/constants';
import useStyles1 from '../common/CommonStyles';
import LocalLibraryIcon from '@material-ui/icons/LocalLibrary';
import SubscriptionsIcon from '@material-ui/icons/Subscriptions';
import HomeWorkIcon from '@material-ui/icons/HomeWork';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    authContainer:{
      minHeight:'80vh',
    },
    formContainer:{
      width:'100%',
      margin:"3rem auto",
      maxWidth:"40vw",
      minWidth:300,
    },
    form:{
      width:'100%',
    },
    chips: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    chip: {
      margin: '0.2rem 0.3rem',
    },
    noLabel: {
      marginTop: theme.spacing(3),
    },
    buttonContainer:{
      margin:"1rem auto",
    },
    title:{
      fontSize:"2rem",
      fontFamily:"Lora",
      fontWeight:400,
      [theme.breakpoints.down('sm')]: {
        fontSize:"1.5rem",
      },
    },
    title1:{
      fontSize:"1.5rem",
      fontFamily:"Lora",
      fontWeight:300,
      [theme.breakpoints.down('sm')]: {
        fontSize:"1.2rem",
      },
    },
  }),
);

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

function getStyles(name: string, genres: string[]) {
  return {
    fontWeight:
      genres.indexOf(name) === -1
        ? 400
        : 500,
  };
}

const updatedGenres = genres.map((val:string)=>(val.charAt(0).toUpperCase()+val.slice(1)));

export interface FormState{
  emailId:string,
  password:string,
  confirmPassword:string,
  username:string,
  address:string,
  zipcode:string,
  genres:string[] | [],
}

const formInitState = {
  emailId:'',
  password:'',
  confirmPassword:'',
  username:'',
  address:'',
  zipcode:'',
  genres:[],
}

const Auth = (props:any) => {
  const classes = useStyles();
  const [formState,setFormState] = useState<FormState>(formInitState);
  const [showPassword,setShowPassword] = useState<boolean[]>([false,false]);
  const [isLogin, setIsLogin] = useState<boolean>(true);
  const [errors,setErrors] = useState({
    username:'',
    password:'',
    emailId:'',
    confirmPassword:'',
  });
  const isDark = useSelector((store:StoreState)=>store.theme.isDark);

  const dispatch = useDispatch();
  const isAuth = useSelector((state:StoreState)=>state.auth.isAuth);

  useEffect(()=>{
    if(isAuth)
    props.history.replace('/');
  },[isAuth]);
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const {name,value} = event.target;
    setFormState(state=>({...state,[name]:value}));
  };
  const showPasswordChange = (isConfirm:boolean)=>{
    setShowPassword(p=>(isConfirm?[p[0],!p[1]]:[!p[0],p[1]]));
  }
  const resetForm = ()=>setFormState(formInitState);
  const toggleIsLogin = () =>setIsLogin(p=>!p);
  const validate = () =>{
    let usernameValid = true;
    let emailValid = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(String(formState.emailId).toLowerCase());
    let passwordValid = formState.password.length >=6;
    let confirmPasswordValid = (formState.password ===formState.confirmPassword) && !isLogin;
    setErrors({
      username:'',
      emailId:emailValid?'':'Enter a valid email id.',
      password:passwordValid?'':'Password should have minimum 6 characters.',
      confirmPassword:(confirmPasswordValid&&!isLogin)?'':'Password and confirm password does not match.'
    });
    return isLogin?emailValid&&passwordValid:usernameValid&&emailValid&&passwordValid&&confirmPasswordValid;
  }
  const handleGenreChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setFormState(state=>({...state,genres:event.target.value as string[]}));
    console.log(formState.genres);
  };
  const onSubmitHandler = () =>{
    if(validate()){
      console.log('validatted');
    if(!isLogin)
    dispatch(signUpMethod(formState));
    else
    dispatch(loginMethod(formState));
    }
  }

  return (
  <Grid container justify='space-around' alignItems='center'  className={classes.authContainer}>
          <Grid item xs={10} md={7}>
            <Typography align='left' className={classes.title}>
            Why you should use {app_name}?
            </Typography>
            <List>
            <ListItem>
            <ListItemIcon><LocalLibraryIcon color='primary' fontSize='large'/></ListItemIcon>
            <ListItemText primary={<Typography className={classes.title1}>Explore the millions of books &amp; magazines.</Typography>} />
          </ListItem>
            <ListItem>
            <ListItemIcon><SubscriptionsIcon color='secondary' fontSize='large' /></ListItemIcon>
            <ListItemText primary={<Typography className={classes.title1}>Get to know about newest release of your favourite authors.</Typography>} />
          </ListItem>
    <ListItem>
      <ListItemIcon><HomeWorkIcon color='action' fontSize='large'/></ListItemIcon>
      <ListItemText primary={<Typography className={classes.title1}>Get to read your book within 3 days of order!</Typography>}/>
    </ListItem>
    </List>
    </Grid>
        <Grid item xs={12} md={4}>
        <Card elevation={20} className={classes.formContainer}>
        <CardContent>
          <Typography variant='h5'>{isLogin?'Login Form':'SignUp Form'}</Typography>
          <form>
          {!isLogin && <TextField margin='dense' fullWidth label='Username' value={formState.username} onChange={handleChange} name='username' variant='filled' />}
          <TextField margin='dense' fullWidth label='Email' value={formState.emailId} onChange={handleChange} name='emailId' variant='filled'
          helperText={errors.emailId}
          />

            <TextField margin='dense' fullWidth label='Password' value={formState.password} onChange={handleChange} name='password' variant='filled' type={showPassword[0]?'text':'password'}  
            helperText={errors.password}
            InputProps={{
              endAdornment:
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={()=>showPasswordChange(false)}
                  >
                    {showPassword[0] ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
            }} />
            {!isLogin && <TextField margin='dense' fullWidth label='Confirm Password' value={formState.confirmPassword} onChange={handleChange} 
            helperText={errors.confirmPassword} name='confirmPassword' variant='filled' type={showPassword[1]?'text':'password'}  InputProps={{
              endAdornment:
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={()=>showPasswordChange(true)}
                  >
                    {showPassword[1] ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
            }} />}
          {!isLogin && <TextField margin='dense'
           id="filled-select-currency"
           select
           fullWidth
           label="Select Your favourite Genres!"
           SelectProps={{
            multiple:true,
            renderValue:(selected) => (
              <div className={classes.chips}>
                {(selected as string[]).map((value) => (
                  <Chip key={value} label={<Typography variant='h6'>{value}</Typography>} className={classes.chip} />
                ))}
              </div>
            ),
            MenuProps
           }}
           value={formState.genres}
           onChange={handleGenreChange}
           variant="filled"
          >
          {updatedGenres.map((name) => (
            <MenuItem key={name} value={name} style={getStyles(name, formState.genres)}>
              {name}
            </MenuItem>
          ))}  
          </TextField>}

      <Grid container justify="center" spacing={2} className={classes.buttonContainer}>
            <Grid item xs={8} md={4}>
              <Button size='large' variant={isDark?'contained':'outlined'} type='button' fullWidth onClick={onSubmitHandler} color='primary'>{isLogin ? 'LOGIN' : 'SIGNUP'}</Button>
            </Grid>
            <Grid item xs={8} md={6}>
              <Button size='large' variant={isDark?'contained':'outlined'} type="button" fullWidth onClick={resetForm} color='primary'>RESET FORM</Button>
            </Grid>
            <Grid item xs={8} md={6}>
              <Button size='large' variant={isDark?'contained':'outlined'} type="button" fullWidth onClick={toggleIsLogin} color='primary'>Switch to {isLogin ? 'SIGNUP' : 'LOGIN'}</Button>
            </Grid>
          </Grid>
          </form>
        </CardContent>
      </Card>
          </Grid>
  </Grid> 
  )
}

export default Auth
