import { Grid, Button } from '@material-ui/core'
import React from 'react'
import { useSelector } from 'react-redux';
import useStyles from '../common/CommonStyles'
import { StoreState } from '../common/interfaces';
import CartItem from '../components/Book/CartItem';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';
import { NavLink} from "react-router-dom";
import * as routes from '../common/routes';

const Favourites = () => {
  const classes = useStyles();
  const liked = useSelector((store:StoreState)=>store.user.liked);
  if(liked===null || liked===undefined || liked.length===0)
  return (
    <div>
      <h1>Looks like you haven't added any book to your favourites!</h1>
    </div>
  )

  return (
    <React.Fragment>
    <Grid container justify='center' className={classes.cartContainer} spacing={2}>
      {liked?.map((item)=><Grid item xs={10} sm={4} md={3}><CartItem cartItemInfo={item} isCart={false}></CartItem></Grid>)}
    </Grid>
    <Grid container justify='center'>
      <Grid item xs={10} sm={4} md={3}>
      <Button
      startIcon={<MeetingRoomIcon/>}
      variant='contained'
      fullWidth
      size='large'
      color="secondary">
      <NavLink
      to={routes.HOMEPAGE}
      key={'EXPLORE'}
      exact
      className={classes.menuOption}    
      >EXPLORE MORE BOOKS</NavLink></Button>
      </Grid>
    </Grid>
    </React.Fragment>
  )
}

export default Favourites
