import { createStyles, Grid, makeStyles, Theme, CardMedia, Typography, Divider, Button, Dialog, DialogTitle, DialogContent, DialogActions, CircularProgress,IconButton, Tooltip, withStyles} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux'
import { CartItem, StoreState } from '../common/interfaces'
import {Rating} from '@material-ui/lab' 
import { useEffect } from 'react';
import { getBookAction } from '../store/actions';
import { useState } from 'react';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { ADD_TO_CART, CLEAR_MESSAGE, LIKE, REMOVE_FROM_CART, SET_MESSAGE, UNLIKE, UPDATE_USER } from '../store/actions/actions';
import seedrandom from 'seed-random';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container:{
      padding:"3rem",
      minHeight:"95vh",
      width:"99vw",
      [theme.breakpoints.down('sm')]: {
        padding:"0.5rem",
      },
    },
    title:{
      fontSize:"3rem",
      fontFamily:"Lora",
      fontWeight:600,
      [theme.breakpoints.down('sm')]: {
        fontSize:"2rem",
      },
    },
    description:{
      fontSize:"1.2rem",
      [theme.breakpoints.down('sm')]: {
        fontSize:"1rem",
      },
      overflow:"hidden",
      textOverflow:"ellipsis",
      display:"-webkit-box",
      WebkitLineClamp:5,
      WebkitBoxOrient:"vertical",
    },
    image:{
      height:500,
      width:300,
      margin:"1rem auto",
      boxShadow:"0 0 2rem rgba(0,0,0,0.4)",
      [theme.breakpoints.down('sm')]: {
        height:350,
        width:250,
      },
    },
    my3:{
      margin:"0.5rem auto 1.5rem"
    },
    myprogress:{
      margin:"5rem auto",
    },
    buttonContainer:{
      margin:"1rem 0.5rem",
    },
    liked:{
      backgroundColor:'#d50000',
      '&:hover':{
        backgroundColor:'#f44336',
      }
    },
    cart:{
      backgroundColor:'#1faa00',
      '&:hover':{
        backgroundColor:'#64dd17'
      }
    },
    toolTip:{
      fontSize:'1rem',
    }
  }),
);

const HtmlTooltip = withStyles((theme: Theme) => ({
  tooltip: {
    maxWidth: 220,
    fontSize: '1rem',
  },
}))(Tooltip);

const ExploreBook = (props:any) => {
  const [open, setOpen] = useState(false);
  const [bookInfo,loading,isAuth] = useSelector((state:StoreState)=>[state.filter.selectedBook,state.common.loading,state.auth.isAuth]);
  const [isLiked, setIsLiked] = useState(false);
  const [isAddedToCart, setIsAddedToCart] = useState(false);
  const generator = seedrandom(bookInfo?.id || '');
  const {liked,cart} = useSelector((store:StoreState)=>store.user);
  const classes = useStyles();
  const dispatch = useDispatch();
  const discount = +(generator()*10+15).toFixed(0);
  const price = bookInfo?.saleInfo?.listPrice|| {
      "amount": Math.floor((200+generator()*500)),
      "currencyCode": "INR"
  };

  useEffect(()=>{
    if(bookInfo === null){
      dispatch(getBookAction(props.match.params['id']));
    }
  },[])
  
  useEffect(()=>{
    if((liked!==null)&&(liked!==undefined)&&(liked.findIndex((item)=>item.id===bookInfo?.id)!==-1)){
    setIsLiked(true);
    }else
    setIsLiked(false);
    if((cart!==null)&&(cart!==undefined)&&(cart.find((item:CartItem)=>item.id===bookInfo?.id)!==undefined)){
    setIsAddedToCart(true);
    }else
    setIsAddedToCart(false);
  })
  
  const book = bookInfo?.volumeInfo;
  const authors = book?.authors.reduce((acc:string,val:string)=>`${acc} | ${val}`,'')?.slice(2);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const toggleLiked = () =>{
    if(isLiked)
    dispatch({type:UNLIKE,id:bookInfo.id});
    else
    dispatch({type:LIKE,id:bookInfo.id,price:price.amount,bookInfo,discount});
    dispatch({type:UPDATE_USER});
    dispatch({type:SET_MESSAGE,message:{severity:isLiked?'error':'success',message:`${book?.title} ${isLiked?'removed from':'added to'} favourites!`}});
    setTimeout(()=>{
      dispatch({type:CLEAR_MESSAGE});
    },3000);
    setIsLiked(p=>!p);
  }
  const toggleCart = () =>{
    console.log(discount);
    if(isAddedToCart)
    dispatch({type:REMOVE_FROM_CART,id:bookInfo.id});
    else
    dispatch({type:ADD_TO_CART,id:bookInfo.id,price:price.amount,bookInfo,discount});
    dispatch({type:UPDATE_USER});
    dispatch({type:SET_MESSAGE,message:{severity:isAddedToCart?'error':'success',message:`${book?.title} ${isAddedToCart?'removed from':'added to'} cart!`}});
    setTimeout(()=>{
      dispatch({type:CLEAR_MESSAGE});
    },3000);
    setIsAddedToCart(p=>!p);
  }

  return( (bookInfo===null || bookInfo===undefined || loading) ?
  <CircularProgress className={classes.myprogress} size={120} thickness={4}/>:
  <Grid className={classes.container} container justify="center" spacing={2}>
      <Grid item container alignItems="center" xs={10} md={4}>
      <CardMedia
          className={classes.image}
          image={book?.imageLinks?.thumbnail}
          title={book?.title}
        />
      </Grid>
      <Grid item container alignItems="center" xs={11} md={6}>
        <div>
        <Typography className={classes.title} align="left">{book?.title}</Typography>
        <Grid item container xs={12} justify="space-between">
          <Grid item container xs={12} sm={6}>
            <Grid item xs={12}>
            <Typography className={classes.description} align="left">By {authors}</Typography>
            </Grid>
            <Grid item xs={12}>
            {book?.averageRating && <Typography align="left"><Rating defaultValue={book?.averageRating} readOnly precision={0.1}></Rating>  :  {book?.ratingsCount} ratings</Typography> }
            </Grid>
            <Grid item xs={12}>
            <Typography className={classes.description} align="left">Price:-<del>{((price?.amount*100)/(100-discount)).toFixed(0)}</del>&nbsp;<em>{price?.amount}</em>&nbsp;{price?.currencyCode}</Typography>
            </Grid>
            <Grid item xs={12}>
            {!isAuth && <Typography className={classes.description} align="left">Please login to buy this book!</Typography>}
            </Grid>
          </Grid>
          <Grid item container xs={12} sm={6} spacing={2} alignItems="center">
            <Grid item xs={4}>
            <HtmlTooltip className={classes.toolTip} arrow title={isAuth?"Like":"Login to like this book!"}>
            <span><IconButton disabled={!isAuth} className={classes.liked} onClick={toggleLiked}>{isLiked ? <FavoriteIcon fontSize="large"/> : <FavoriteBorderIcon fontSize="large"/>}</IconButton></span></HtmlTooltip>
            </Grid>
            <Grid item xs={4}>
            <HtmlTooltip className={classes.toolTip} arrow title={isAuth?"Add to cart":"Login to add this book to cart!"}>
              <span>
            <IconButton disabled={!isAuth} className={classes.cart} onClick={toggleCart}>{isAddedToCart ? <ShoppingCartIcon fontSize="large"/>:<AddShoppingCartIcon fontSize="large"/>}</IconButton></span></HtmlTooltip>
            </Grid>
          </Grid>
        </Grid>
        <Divider className={classes.my3}></Divider>
        <Typography align="left" paragraph={true} className={classes.description}>
        {book?.description.replace(/<p>/g,'').replace(/<\/p>/g,'').replace(/<i>/g,'').replace(/<\/i>/g,'')}
        </Typography>
        <Grid container className={classes.buttonContainer}>
          <Grid item xs={8} sm={4}>
          <Button size="large" variant="contained" fullWidth color="primary" onClick={handleClickOpen} >Read More</Button>
          </Grid>
        </Grid>
        </div>
      </Grid>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title">
          <Typography className={classes.title}>
          {book?.title}
          </Typography>
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            {book?.description.replace(/<p>/g,'').replace(/<\/p>/g,'').replace(/<i>/g,'').replace(/<\/i>/g,'')}
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button autoFocus variant="contained" size="large" onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </Grid>
  )
}

export default ExploreBook
