import React, { useEffect } from 'react';
import {Route, Switch, Redirect} from 'react-router-dom';
import './App.css';
import Navbar from './components/Header/Navbar';
import { ThemeProvider } from '@material-ui/styles';
import Home from './containers/Home';
import firebase from 'firebase';
import { firebaseConfig } from './common/constants';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import ExploreBook from './containers/ExploreBook';
import Auth from './containers/Auth';
import { autoLogin } from './store/actions/auth';
import {createMuiTheme } from '@material-ui/core';
import * as routes from './common/routes';
import CheckOut from './containers/CheckOut';
import Cart from './containers/Cart';
import Message from './components/Message/Message';
import Favourites from './containers/Favourties';
import Explore from './containers/Explore';
import Orders from './containers/Orders';
import { TOGGLE_THEME } from './store/actions/actions';
import NavBar from './components/Header/Navbar';
import useStyles from './common/CommonStyles';
import { StoreState } from './common/interfaces';

const App = (props:any) => {
  const [darkMode,setDarkMode] = useState(true);
  const isAuth = useSelector((store:StoreState)=>store.auth.isAuth);
  const classes = useStyles();
  const dispatch = useDispatch();
  const theme = createMuiTheme({
    palette: {
      type: darkMode ? "dark" : "light",
      background:{
        paper: darkMode? '#333333' : '#ffffff',
        default: darkMode? '#111111' : '#fafafa',
      }
    }
  })
  
  const toggleTheme = ()=>{
    console.log(theme.palette.type);
    dispatch({type:TOGGLE_THEME});
    setDarkMode(p=>!p);
  }
  useEffect(()=>{
    if (!firebase.apps.length)
      firebase.initializeApp(firebaseConfig);
      dispatch(autoLogin());
  },[]);

  return (
    <div className="App">
      <ThemeProvider theme={theme}>
            <div className={classes.h100vh}>
            <Route path={routes.HOMEPAGE} render={(props)=><NavBar toggleTheme={toggleTheme} {...props} />}></Route>
            <Route path={routes.HOMEPAGE} component={Message}></Route>
            {/* =============================================> */}
            <Switch>
            <Route path={routes.HOMEPAGE} component={Home} exact></Route>
            <Route path={routes.EXPLORE} exact component={Explore}/>
            <Route path={routes.BOOK_INFO} exact component={ExploreBook}/>
            <Route path={routes.AUTH} exact  component={Auth}/>
            {isAuth && <React.Fragment><Route path={routes.CART} component={Cart} exact />
            <Route path={routes.FAVOURITES} component={Favourites} exact/>
            <Route path={routes.CHECKOUT} component={CheckOut} exact />
            <Route path={routes.ORDERS} component={Orders} exact /></React.Fragment>}
            <Redirect to={routes.HOMEPAGE}></Redirect>
            </Switch>
            </div>
      </ThemeProvider>
    </div>
  );
}

export default App;
