import React from 'react'
import {Card,CardActionArea,CardMedia} from '@material-ui/core';
import useStyles from '../../common/CommonStyles';

const Book2 = (props:any) => {
  const book = props.book.volumeInfo;
  const classes = useStyles();
  const goToBook=()=>props.goToBook(props.book.id);
  return (
    <div>
      <Card className={classes.book2Image} elevation={20}>
        <CardActionArea onClick={goToBook}>
        <CardMedia
          className={classes.book2Image}
          image={book?.imageLinks?.thumbnail}
          title={book.title}
        />
        </CardActionArea>
      </Card>
    </div>
  )
}

export default React.memo(Book2)
