import axios from 'axios';
import { Query } from './interfaces';

export const getBook = async(id:string) =>{
  const response = await axios.get(`https://www.googleapis.com/books/v1/volumes/${id}?fields=id,volumeInfo(title,authors,publisher,publishedDate,description,averageRating,ratingsCount,categories,imageLinks),saleInfo`);
  const book = await response.data;
  return book;
}

export const getBooksApi = async(query:Query,startIndex:number,maxResults:number)=>{
  const response = await axios.get(`https://www.googleapis.com/books/v1/volumes/?q=${query.general}${query.title!==null?'+intitle='+URLify(query.title):''}${query.author!==null?'+inauthor='+URLify(query.author):''}${query.genre!==null?'+subject='+URLify(query.genre):''}&fields=totalItems,items(id,volumeInfo(title,authors,publisher,publishedDate,description,averageRating,ratingsCount,categories,imageLinks),saleInfo)&startIndex=${startIndex}&maxResults=${maxResults}`);
  const books = await response.data;
  console.log(books);
  return books || null;
}

export const URLify = (word:string)=>word.replaceAll(' ','%20');