import { Button, createStyles, Grid, makeStyles, Theme, Typography,CardMedia} from '@material-ui/core';
import {Rating} from '@material-ui/lab';
import React from 'react'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    title:{
      fontSize:"1.5rem",
      fontWeight:600,
      [theme.breakpoints.down('md')]:{
        fontSize:'1rem',
      },
      [theme.breakpoints.down('sm')]:{
        fontSize:'0.6rem',
      }
    },
    subtitle:{
      fontSize:"1rem",
      [theme.breakpoints.down('md')]:{
        fontSize:'0.8rem',
      },
      [theme.breakpoints.down('sm')]:{
        fontSize:'0.5rem',
      }
    },
    container:{
      backgroundColor: (props:any) => props.color,
      padding:"1rem 0.5rem",
      margin:"1rem",
      borderRadius:"0.5rem",
      boxShadow:"0 0 0.5rem #000000",
      minHeight:200,
      [theme.breakpoints.up('md')]:{
        minHeight:320,
      },
    },
    shortText:{
      overflow:"hidden",
      textOverflow:"ellipsis",
      display:"-webkit-box",
      WebkitLineClamp:4,
      WebkitBoxOrient:"vertical",
      [theme.breakpoints.down('md')]:{
        fontSize:'0.8rem',
      },
      [theme.breakpoints.down('sm')]:{
        fontSize:'0.6rem',
      }
    },
    image:{
      height:250,
      width:150,
      margin:"auto",
      boxShadow:"0 0 0.5rem #000000",
      [theme.breakpoints.down('md')]:{
        height:160,
        width:90,
      },
      [theme.breakpoints.down('xs')]:{
        height:100,
        width:60,
      }
    },
    h100:{
      height:'35vh',
      [theme.breakpoints.up('md')]:{
        minHeight:320,
      }
    },
  }),
);

const Book1 = (props:any) => {
  const classes = useStyles({
    color:props.color,
  });
  const goToBook=()=>props.goToBook(props.book.id);
  const book = props.book.volumeInfo;
  return (
    <div className={classes.container}>
    <Grid container spacing={2} justify="space-around" alignItems='center' alignContent='center' className={classes.h100}>
      <Grid item xs={3} lg={5}>
      <CardMedia component='img' image={book?.imageLinks?.thumbnail} alt={book.title} title={book.title} className={classes.image}></CardMedia>
      </Grid>
      <Grid item container xs={8} lg={7} alignContent="space-between">
      <Grid item xs={12}>
      {book?.title && <Typography variant="h6" align="left" className={classes.title}>{book?.title}</Typography>}
      {book?.authors && <Typography align="left" variant="body1" className={classes.subtitle}>by {book?.authors[0]}</Typography>}
      
      {book.averageRating && <Typography align="left"  className={classes.subtitle}><Rating defaultValue={book.averageRating} readOnly precision={0.1}></Rating><br/>{book.ratingsCount} ratings</Typography> }
      
      {book?.description && <Typography variant="body1" align="justify" className={classes.shortText}>{book?.description.replace(/<p>/g,'').replace(/<\/p>/g,'').replace(/<i>/g,'').replace(/<\/i>/g,'')}</Typography>}
      </Grid>
      <Grid item xs={12}>
      <Button fullWidth variant='contained' size='small' color="primary" onClick={goToBook}>Read More</Button>
      </Grid>
      </Grid>
      </Grid>
      </div>
  )
}

export default React.memo(Book1);
