import React from 'react'
import useStyles from '../../common/CommonStyles'
import { Order as OrderInterface } from '../../common/interfaces'
import {Card, Grid, Typography, TableCell, TableContainer,Table, TableRow,Divider, TableHead, TableBody, Paper} from '@material-ui/core';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { SET_IS_DELIVERED, UPDATE_USER } from '../../store/actions/actions';

interface IProps{
  order:OrderInterface,
  index:number
}

const Order:React.FC<IProps> = ({order,index}) => {
  const classes = useStyles();
  const date = new Date(order.date);
  const deliveryDate = new Date(order.deliveryDate);
  const dispatch = useDispatch();
  let total = 0;
  order?.orderItems?.forEach((item)=>{
    // originalPrice = originalPrice + (100*item.price*item?.quantity)/(100-item.discount);
    total = total + item.price*item?.quantity});
  
    useEffect(()=>{
    if(!order.isDelivered && order.deliveryDate < (Date.now())){
      dispatch({type:SET_IS_DELIVERED,index});
      dispatch({type:UPDATE_USER});
    }
  },[]);

  console.log(order);
  return (
    <Card elevation={12} className={classes.cartContainer}>
    <Typography align='left' variant='h5'>Order Status: {order.isDelivered?'Completed':'To be delivered'}</Typography>
    <Typography variant='subtitle1' align="left">Ordered on : {date?.toDateString()}</Typography>
    <Typography variant='subtitle1' align="left">Date of delivery : {deliveryDate?.toDateString()}</Typography>
    <Divider></Divider>
    <Grid container justify='center'>
      <Grid item xs={12} sm={10} md={8}>
      <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="center">Book</TableCell>
            <TableCell align="center">Quantity</TableCell>
            <TableCell align="center">Total Price</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {order?.orderItems?.map((item)=><TableRow>
            <TableCell align="center">{item?.bookInfo?.volumeInfo?.title}</TableCell>
            <TableCell align="center">{item?.quantity}</TableCell>
            <TableCell align="center">&#8377;{(item?.price*item?.quantity).toFixed(2)}</TableCell>
          </TableRow>)}
        </TableBody>
        <TableRow>
          <TableCell align='center'>Total</TableCell>
          <TableCell/>
          <TableCell align='center'>&#8377;{total.toFixed(2)}</TableCell>
        </TableRow>
        </Table>
        </TableContainer>
      </Grid>
    </Grid>
    </Card>
  )
}

export default Order
