import React from 'react'
import useStyles from '../common/CommonStyles'
import {Grid} from '@material-ui/core';
import { useSelector } from 'react-redux';
import { StoreState, Order as OrderInterface } from '../common/interfaces';
import Order from '../components/Order/Order';

const Orders = (props:any) => {
  const classes = useStyles();
  const orders = useSelector((store:StoreState)=>store.user.orders);
  if(orders===null || orders===undefined || orders.length===0)
  return (
    <div>
      <h1>There is no order from your side YET!</h1>
    </div>
  )
  return (
    <div>
      <Grid container className={classes.cartContainer} justify='center'>
        {orders?.map((order:OrderInterface,index:number)=><Grid item key={order.date} xs={12} sm={10} md={8}>
          <Order order={order} index={index}/>
        </Grid>)}
      </Grid>
    </div>
  )
}

export default Orders
