import {CircularProgress, Grid, Typography, TextField, Button,  } from '@material-ui/core'
import {Pagination} from '@material-ui/lab';
import React, { useCallback } from 'react'
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useStyles from '../common/CommonStyles'
import { StoreState } from '../common/interfaces';
import Book1 from '../components/Book/Book1';
import { getBookAction } from '../store/actions';
import { getBooks } from '../store/actions/filter';

const Explore = (props:any) => {
  const classes = useStyles();
  const [query,setQuery]=useState<string>('');
  const [exploreBooks,loading,colors,page] = useSelector((store:StoreState)=>[store.filter.exploreBooks,store.common.loading,store.theme.colors,store.filter.page]); 
  const dispatch = useDispatch();

  const onSearch = ()=>{
    dispatch(getBooks({general:query,title:null,author:null,genre:null},9*(page-1),9,1))};
  
  const handlePageChange = (event: React.ChangeEvent<unknown>, value: number) => {
    dispatch(getBooks({general:query,title:null,author:null,genre:null},9*(value-1),9,value));
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) =>setQuery(event.target.value);

  const goToBook = useCallback((id:string) =>{
    dispatch(getBookAction(id));
    props.history.push(`/bookInfo/${id}`);
  },[]);

  const onKeyEnter = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && query!=='') {
      dispatch(getBooks({general:query,title:null,author:null,genre:null},0,9,0));
    }
  };

  return (
    <Grid container className={classes.cartContainer} justify='center'>
      <Grid item xs={11}>
        <Typography align='left' className={classes.title}>Search your next Book!</Typography>
      </Grid>
      <Grid item container spacing={2} xs={11}>
        <Grid item xs={8}>
        <TextField id="my-input" value={query} onChange={handleChange} fullWidth variant='filled' onKeyPress={onKeyEnter} />
        </Grid>
        <Grid item xs={2}>
          <Button variant='contained' onClick={onSearch} fullWidth color='primary' className={classes.h100}>Search</Button>
        </Grid>
      </Grid>
      <Grid item container justify='center' xs={11}>
      <Pagination className={classes.m1} siblingCount={2} count={Math.floor(exploreBooks?.totalItems/9 +1) || 5} page={page} onChange={handlePageChange} />
      </Grid>
      <Grid item container justify='space-around' xs={12}>
        {loading ? <CircularProgress size={70} thickness={5}/> : exploreBooks!==undefined && exploreBooks?.items.map((item:any,index:number)=><Grid item xs={12} sm={6} md={4} key={item.id}>
        <Book1  goToBook={goToBook} key={item.id} book={item} color={colors[index%12]}></Book1>
        </Grid>)}
      </Grid>
    </Grid>
  )
}

export default Explore
