import { createStyles, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    shortText:{
      overflow:"hidden",
      textOverflow:"ellipsis",
      display:"-webkit-box",
      WebkitLineClamp:4,
      WebkitBoxOrient:"vertical",
    },
    m2:{
      margin:"3rem",
    },
    m1:{
      margin:"2rem",
    },
    my2:{
      margin:"2rem auto",
    },
    title:{
      fontSize:"2rem",
      fontFamily:"Lora",
      fontWeight:400,
      [theme.breakpoints.down('sm')]: {
        fontSize:"1.5rem",
      },
    },
    title1:{
      fontSize:"1.5rem",
      fontFamily:"Lora",
      fontWeight:300,
      [theme.breakpoints.down('sm')]: {
        fontSize:"1.2rem",
      },
    },
    description:{
      fontSize:"1.2rem",
      [theme.breakpoints.down('sm')]: {
        fontSize:"1rem",
      },
      overflow:"hidden",
      textOverflow:"ellipsis",
      display:"-webkit-box",
      WebkitLineClamp:5,
      WebkitBoxOrient:"vertical",
    },
    book2Image:{
      width:"100%",
      maxWidth:200,
      height:300,
      [theme.breakpoints.down('sm')]: {
        height:200,
        width:125,
      },
      [theme.breakpoints.down('xs')]: {
        height:150,
        width:100,
      },
    },
    w98vw:{
      width:"95%",
      margin:"1rem auto",
    },
    h100:{
      minHeight:'100%',
    },
    cartContainer:{
      padding:'3rem 0.5rem',
      maxWidth:'99%',
      paddingBottom:'2rem',
    },
    table: {
      minWidth: 200,
    },
    h100vh:{
      minHeight:'100vh',
    },
    menuOption:{
      fontFamily:"Open Sans",
      textDecoration:"none",
      borderRadius:6,
      color:"white",
    },
    menuOptionActive:{
  
    },
  }),
);
export default useStyles;