            "publisher": "Routledge",
            "publishedDate": "2019-10-24",
            "description": "<p>Digital Religion does not simply refer to religion as it is carried out online, but more broadly studies how digital media interrelate with religious practice and belief. This collection explores Digital Hinduism and consequentially studies how Hinduism is expressed in the digital sphere and how Hindus utilise digital media.</p> <p></p> <p>Highlighting digital Hinduism and including case studies with foci on India, Asia and the global Hindu diaspora, this book features contributions from an interdisciplinary and international panel of academics. The chapters focus on specific case studies, which in summary exemplify the wide variety and diversity of what constitutes Digital Hinduism today. </p> <p></p> <p>Applying methods and research questions from various disciplinary backgrounds appropriate to the study of religion and digital culture, such as Religious Studies, South Asian Studies, Anthropology and Media and Communication Studies, this book is vital reading for any scholar interested in the relationship between religion and the digital world. </p>",
