            "description": "<p>WITHOUT THE DARKNESS, LIGHT HAS NO PURPOSE.</p><p>WITHOUT THE VILLAIN, WHAT WOULD THE GODS DO? </p><p>INDIA, 3400 BCE.</p><p>A land in tumult, poverty and chaos. Most people suffer quietly. A few rebel. Some fight for a better world. Some for themselves. Some don't give a damn. Raavan. Fathered by one of the most illustrious sages of the time. Blessed by the Gods with talents beyond all. Cursed by fate to be tested to the extremes.</p><p>A formidable teenage pirate, he is filled with equal parts courage, cruelty and fearsome resolve. A resolve to be a giant among men, to conquer, plunder, and seize the greatness that he thinks is his right.</p><p>A man of contrasts, of brutal violence and scholarly knowledge. A man who will love without reward and kill without remorse.</p><p>This exhilarating third book of the Ram Chandra series sheds light on Raavan, the king of Lanka. And the light shines on darkness of the darkest kind. Is he the greatest villain in history or just a man in a dark place, all the time? </p><p>Read the epic tale of one of the most complex, violent, passionate and accomplished men of all time. </p>",
