import { Grid, Button } from '@material-ui/core'
import React from 'react'
import { useSelector } from 'react-redux';
import useStyles from '../common/CommonStyles'
import { StoreState } from '../common/interfaces';
import CartItem from '../components/Book/CartItem';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';
import { NavLink} from "react-router-dom";
import * as routes from '../common/routes';

const Cart = () => {
  const classes = useStyles();
  const cart = useSelector((store:StoreState)=>store.user.cart);
  if(cart===null || cart===undefined || cart.length===0)
  return (
    <div>
      <h1>No CheckOut Items Over Here</h1>
    </div>
  )

  return (
    <React.Fragment>
    <Grid container justify='center' className={classes.cartContainer} spacing={2}>
      {cart?.map((item)=><Grid item xs={10} sm={4} md={3}><CartItem cartItemInfo={item} isCart={true}></CartItem></Grid>)}
    </Grid>
    <Grid container justify='center'>
      <Grid item xs={10} sm={4} md={3}>
      <Button
      startIcon={<MeetingRoomIcon/>}
      variant='contained'
      fullWidth
      size='large'
      color="primary">
      <NavLink
      to={routes.CHECKOUT}
      key={'CART'}
      exact
      className={classes.menuOption}    
      >CHECKOUT</NavLink></Button>
      </Grid>
    </Grid>
    </React.Fragment>
  )
}

export default Cart
