import { FilterState } from "../../common/interfaces";
import { books, exploreBooks } from "../../static/BooksData";
import { CLEAR_ALL, LOAD_BOOK, LOAD_EXPLORE_BOOKS, SET_CART_BOOKS } from "../actions/actions";

const initState:FilterState = {
  booksCollection:books,
  selectedBook:null,
  exploreBooks:exploreBooks,
  page:1,
  query:null,
};

export default (state = initState, action:any) => {
  switch (action.type) {
    case LOAD_BOOK: return {...state,selectedBook:action.book};
    case SET_CART_BOOKS: return {...state,cartBooks:action.cartBooks};
    case LOAD_EXPLORE_BOOKS: return{...state,exploreBooks:action.books,query:action.query,page:action.page};
    case CLEAR_ALL:return initState;
    default:
      return state;
  }
};