import { Button, Grid } from '@material-ui/core';
import { Typography } from '@material-ui/core';
import React from 'react'
import useStyles from '../../common/CommonStyles'
import { books } from '../../static/BooksData';
import Book2 from '../Book/Book2';

interface Props{
  goToBook:(id:string)=>void;
  goToExplore:()=>void;
}

const DiscoverBooks:React.FC<Props> = ({goToBook,goToExplore}) => {
  const classes = useStyles();
  return (
    <div className={classes.my2}>
      <div className={classes.m2}>
      <Typography align="center" variant='h3'>Discover Your Next Book!</Typography>
      </div>
      <Grid container justify="center" spacing={2} className={classes.w98vw}>
      {books.map((item:any,index:number)=><Grid item xs={5} sm={3} lg={2} key={item.id}>        
        <Book2 book={item} goToBook={goToBook} /></Grid>)}
      <Grid item xs={12} container justify='center'>
      <Grid item xs={10} sm={4}>
      <Button size="large" fullWidth color="primary" variant="contained" onClick={goToExplore} className={classes.my2}>Explore</Button>
      </Grid> 
      </Grid> 
      </Grid>
    </div>
  )
}

export default DiscoverBooks
