import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Order, StoreState } from '../common/interfaces'
import {Grid, Typography,Button,  TableRow, Table, TableContainer,TableHead,TableBody,TableCell, Paper,IconButton} from '@material-ui/core';
import useStyles from '../common/CommonStyles';
import {Delete} from '@material-ui/icons';
import { ADD_ORDER, CLEAR_MESSAGE, REMOVE_FROM_CART, SET_MESSAGE, UPDATE_USER } from '../store/actions/actions';

const CheckOut = (props:any) => {
  const cart = useSelector((store:StoreState)=>store.user.cart);
  const dispatch = useDispatch();
  const classes = useStyles();
  let total = 0;
  let originalPrice=0;
  cart?.forEach((item)=>{
    originalPrice = originalPrice + (100*item.price*item?.quantity)/(100-item.discount);
    total = total + item.price*item?.quantity});
  
    const onDelete = (id:string,name:string)=>{
    dispatch({type:REMOVE_FROM_CART,id});
    dispatch({type:UPDATE_USER});
    dispatch({type:SET_MESSAGE,message:{severity:'success',message:`${name} removed from cart!`}});
    setTimeout(()=>{
      dispatch({type:CLEAR_MESSAGE});
    },3000);
  }
    const onPlaceOrder = () =>{
      const deliveryDate = new Date();
      deliveryDate.setDate(deliveryDate.getDate()+Math.floor(Math.random()*4+1));
      const order:Order = {isDelivered:false,orderItems:cart,date:new Date().getTime(),deliveryDate:deliveryDate.getTime()};
      dispatch({type:ADD_ORDER,order});
      dispatch({type:UPDATE_USER});
      dispatch({type:SET_MESSAGE,message:{severity:'success',message:`Your order has been placed. Happy Reading!`}});
      setTimeout(()=>{
        dispatch({type:CLEAR_MESSAGE});
      },3000);
      props.history.replace('/');
    }

  if(cart===null || cart===undefined || cart.length===0)
  return (
    <div>
      <h1>No CheckOut Items Over Here</h1>
    </div>
  )

  return (
    <div>
      <Grid container justify='center'>
      <Grid item xs={12} sm={4} className={classes.cartContainer} alignItems='flex-start'>
        <Typography variant='h3'>CHECKOUT</Typography>  
      <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="right">Book</TableCell>
            <TableCell align="right">Quantity</TableCell>
            <TableCell align="right">Price per Book</TableCell>
            <TableCell align="right">Offer Price per Book</TableCell>
            <TableCell align="right">Total Price</TableCell>
            <TableCell/>
          </TableRow>
        </TableHead>
        <TableBody>
          {cart.map((item)=><TableRow>
            <TableCell align="right">{item?.bookInfo?.volumeInfo?.title}</TableCell>
            <TableCell align="right">{item?.quantity}</TableCell>
            <TableCell align="right">{((100*item?.price)/(100-item?.discount)).toFixed(2)}</TableCell> 
            <TableCell align="right">&#8377;{item?.price}</TableCell>
            <TableCell align="right">&#8377;{(item?.price*item?.quantity).toFixed(2)}</TableCell>
            <TableCell align='right'><IconButton onClick={()=>onDelete(item.id,item?.bookInfo?.volumeInfo?.title)}><Delete/></IconButton></TableCell>
          </TableRow>)}
        </TableBody>
        <TableRow>
          <TableCell align='right'>Total</TableCell>
          <TableCell/>
          <TableCell/>
          <TableCell/>
          <TableCell align='right'>&#8377;{total.toFixed(2)}</TableCell>
        </TableRow>
        </Table>
        </TableContainer>
      </Grid>
      </Grid>
      <Grid container justify='center'>
        <Grid item xs={10} sm={4} md={3} lg={2}>
          <Button variant='contained' onClick={onPlaceOrder} fullWidth color='primary' size='large'>Place Order</Button>
        </Grid>
      </Grid>
    </div>
  )
}

export default CheckOut;
