import { Dispatch } from "redux"
import { getBook, getBooksApi } from "../../common/api";
import { CartItem, Query } from "../../common/interfaces";
import { LOAD_BOOK, LOAD_BOOK_ERROR, LOAD_EXPLORE_BOOKS, LOAD_EXPLORE_BOOKS_ERROR, SET_CART_BOOKS, SET_ERROR, TOGGLE_LOADING } from "./actions"

export const getBookAction = (name:string) =>async (dispatch:Dispatch)=>{
    dispatch({type:TOGGLE_LOADING});
    try {
      const book =  await getBook(name);
      dispatch({type:LOAD_BOOK, book});
    } catch (error) {
      dispatch({type:LOAD_BOOK_ERROR,error});
    }
    dispatch({type:TOGGLE_LOADING});
 }

export const getBooks = (query:Query,startIndex:number,maxResults:number,page:number)=>async (dispatch:Dispatch)=>{
  dispatch({type:TOGGLE_LOADING});
  try {
    const books =  await getBooksApi(query,startIndex,maxResults);
    dispatch({type:LOAD_EXPLORE_BOOKS, books,query,page});
  } catch (error) {
    dispatch({type:LOAD_EXPLORE_BOOKS_ERROR,error});
  }
  dispatch({type:TOGGLE_LOADING});
}

export const getSelectedBooks = (cart:CartItem[]|undefined|null)=>{
  return async (dispatch:Dispatch)=>{
    getSelectedBookMethod(cart,dispatch);
  }
}

export const getSelectedBookMethod = async (cart:CartItem[]|undefined|null,dispatch:Dispatch)=>{
  dispatch({type:TOGGLE_LOADING});
  if(cart===undefined || cart===null || cart.length===0){
    dispatch({type:SET_ERROR,error:{code:'CART IS EMPTY',message:'Looks like your cart is empty. Explore your next read in explore section!'}});
  }else
  try {
    let cartBooks:any[] = [];
    await cart?.forEach(async (item)=>{
      const book = await getBook(item.id);
      cartBooks.push(book);
    });
    dispatch({type:SET_CART_BOOKS,cartBooks});
  } catch (error) {

  }
  dispatch({type:TOGGLE_LOADING});
}