import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import * as routes from '../../common/routes';
import { AppBar, Button, CssBaseline, Divider, fade, Hidden, IconButton, List, ListItem, ListItemIcon, ListItemText, Toolbar, Typography } from '@material-ui/core';
import { NavLink} from "react-router-dom";
import MenuIcon from "@material-ui/icons/Menu"
import ExploreIcon from '@material-ui/icons/Explore';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { useDispatch, useSelector } from 'react-redux';
import { StoreState } from '../../common/interfaces';
import ListAltIcon from '@material-ui/icons/ListAlt';
import FavoriteIcon from '@material-ui/icons/Favorite';
import Brightness5Icon from '@material-ui/icons/Brightness5';
import Brightness4Icon from '@material-ui/icons/Brightness4';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { signOut } from '../../store/actions/auth';
import { app_name } from '../../common/constants';

const drawerWidth = 300;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  list:{
    width: drawerWidth,
    height: "100%",
  },
  title: {
    fontSize:"1.4rem",
    fontFamily: "Shadows Into Light Two",
    fontWeight:600,
    letterSpacing:3,
    marginRight: theme.spacing(2),
    textDecoration:"none"
  },
  whitespace:{
    flexGrow:1,
  },
  menuOption:{
    fontSize:"1.2rem",
    fontFamily:"Open Sans",
    margin:theme.spacing(1),
    backgroundColor:"transparent",
    textDecoration:"none",
    borderRadius:6,
    height:"100%",
    color:'inherit',
  },
  menuOptionButton:{
    fontSize:'1.2rem',
    height:'100%',
    fontFamily:"Open Sans",
  },
  menuOptionActive:{
    color:'#0f0',
  },
  menuOptionActive1:{
    color:'#00f',
  },
  link: {
    textDecoration: "none", 
    fontFamily:"Open Sans",
    width:drawerWidth,
    color:theme.palette.text.primary,
    "&:hover":{
    }
  },
  linkActive: {
    textDecoration: "none",
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  // necessary for content to be below app bar
  drawerPaper: {
    width: '100%',
    background:"linear-gradient(to bottom right, #212121, #777777) center center/cover no-repeat",
    height: "100%",
  },
  content: {
    marginTop:"4rem",
    width:"100%",
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    margin:"auto 0.5rem",
    width: '16rem',
    display:"flex",
  },
  inputRoot: {
    color: 'white',
  },
  inputInput: {
    // vertical padding + font size from searchIcon
    paddingLeft:2,
    transition: theme.transitions.create('width'),
    width: '100%',
    fontSize:"1rem",
  },
  drawerIcon:{
    color:"inherit"
  },
  button:{
    width:"100%",
    maxWidth:drawerWidth,
    fontSize:"1.4rem",
    fontWeight:600,
  },
  appBar:{
    height:24,
  }
}));

const unAuthMenu = [
  {
    title: "EXPLORE",
    icon: <ExploreIcon color='inherit'></ExploreIcon>,
    path: routes.EXPLORE,
  },
];

const authMenu = [
  {
    title: "EXPLORE",
    icon: <ExploreIcon></ExploreIcon>,
    path: routes.EXPLORE,
  },
  {
    title: "CART",
    icon: <ShoppingCartIcon></ShoppingCartIcon>,
    path: routes.CART,
  },
  {
    title: "FAVOURITES",
    icon: <FavoriteIcon></FavoriteIcon>,
    path: routes.FAVOURITES,
  },
  {
    title: "MY ORDERS",
    icon: <ListAltIcon></ListAltIcon>,
    path: routes.ORDERS,
  },
]



const NavBar = (props:any) => {
  const classes = useStyles();
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [isAuth,isDark] = useSelector((store:StoreState)=>[store.auth.isAuth,store.theme.isDark]); 
  const dispatch = useDispatch();
  const menuArr = isAuth?authMenu:unAuthMenu;
  const handleDrawerToggle = () => {
    setMobileOpen(mobileOpen=>!mobileOpen);
  };
  const toggleTheme = ()=>{
    setMobileOpen(false);
    props.toggleTheme()};
  const goToAuth = () =>{
    setMobileOpen(false);
    if(isAuth){
    console.log('Helo');
    dispatch(signOut());
    props.history.replace('/');
    }else{
    props.history.replace('/auth');
    }
  }
  const goToPage=(path:string)=>{
    setMobileOpen(false);
    props.history.push(path);
  }
  const menuOptions = <Hidden smDown>
    {menuArr.map((item,index)=><React.Fragment><NavLink
      to={item.path}
      key={item.title}
      exact
      activeClassName={isDark ? classes.menuOptionActive : classes.menuOptionActive1}
      className={classes.menuOption}    
      ><Button 
      startIcon={item.icon}
      color="inherit"
      className={classes.menuOptionButton}
      >
        {item.title}
        </Button></NavLink></React.Fragment>)}
      <Button className={classes.menuOption} startIcon={isDark?<Brightness5Icon/>:<Brightness4Icon/>} variant="text" color="inherit" size="large" onClick={toggleTheme} >Change Theme</Button>      
      <div className={classes.whitespace}></div>

      <Button className={classes.menuOption} startIcon={<ExitToAppIcon/>} variant="text" color="inherit" size="large" onClick={goToAuth}>{isAuth?"LOGOUT":"LOGIN/SIGNUP"}</Button>
  </Hidden>

const drawer = (
  <div>
    <Divider />
    <List className={classes.list}>
      {menuArr.map((item, index) => (
        <ListItem button className={classes.link}  onClick={()=>goToPage(item.path)}>
        <ListItemIcon className={classes.drawerIcon}>{item.icon}</ListItemIcon>
        <ListItemText primary={item.title} />
      </ListItem>
      ))}
    <ListItem button className={classes.link}  onClick={toggleTheme}>
      <ListItemIcon className={classes.drawerIcon}>{isDark?<Brightness5Icon/>:<Brightness4Icon/>}</ListItemIcon>
      <ListItemText primary='Change Theme' />
    </ListItem>
    <ListItem button className={classes.link}  onClick={goToAuth}>
      <ListItemIcon className={classes.drawerIcon}><ExitToAppIcon/></ListItemIcon>
      <ListItemText primary={isAuth?"LOGOUT":"LOGIN/SIGNUP"} />
    </ListItem>
    </List>
  </div>
);

const container =
window !== undefined ? () => window().document.body : undefined;

  return (
<div className={classes.root}>
      <CssBaseline />
      <AppBar elevation={14} color='transparent'  position="sticky">
        <Toolbar>
        <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}>
            <MenuIcon />
          </IconButton>
          <NavLink to={routes.HOMEPAGE} className={classes.title}>
          <Typography variant="h5" color="primary" className={classes.title} noWrap>
            {app_name.toUpperCase()}
          </Typography>
          </NavLink>
          {menuOptions}
        </Toolbar>
      </AppBar>
      <nav aria-label="mailbox folders">
          <SwipeableDrawer
            container={container}
            variant="temporary"
            transitionDuration={600}
            anchor="left"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            onOpen={handleDrawerToggle}
            ModalProps={{
              keepMounted: false, // Better open performance on mobile.
            }}>
            {drawer}
          </SwipeableDrawer>
      </nav>
    </div>
  );
}

export default NavBar;