import React, { useState } from 'react'
import {Card, Grid, Typography, CardMedia, CardContent, makeStyles, IconButton, CardHeader,FormControl,InputLabel,Input} from '@material-ui/core';
import {Rating} from '@material-ui/lab';
import DeleteIcon from '@material-ui/icons/Delete';
import { useDispatch } from 'react-redux';
import { ADD_TO_CART, CLEAR_MESSAGE, REMOVE_FROM_CART, SET_MESSAGE, UNLIKE, UPDATE_USER } from '../../store/actions/actions';

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    minHeight:410,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  avatar: {
    backgroundColor: '#f00',
  },
  description:{
    fontSize:"1.2rem",
    [theme.breakpoints.down('sm')]: {
      fontSize:"1rem",
    },
    overflow:"hidden",
    textOverflow:"ellipsis",
    display:"-webkit-box",
    WebkitLineClamp:5,
    WebkitBoxOrient:"vertical",
  },
}));

const CartItem = (props:any) => {
  const classes = useStyles();
  const {id,bookInfo,price,discount} = props.cartItemInfo;
  const {isCart} = props;
  const [quantity,setQuantity]=useState<number>(props?.cartItemInfo?.quantity);
  const book = bookInfo?.volumeInfo;
  const authors = book?.authors.reduce((acc:string,val:string)=>`${acc} | ${val}`,'')?.slice(2);
  const dispatch = useDispatch();
  const onDelete = ()=>{
    if(isCart){
    dispatch({type:REMOVE_FROM_CART,id});
    }else{
      dispatch({type:UNLIKE,id});
    }
    dispatch({type:UPDATE_USER});
    dispatch({type:SET_MESSAGE,message:{severity:'success',message:`${book?.title} removed from ${isCart?'cart':'favourites'}!`}});
    setTimeout(()=>{
      dispatch({type:CLEAR_MESSAGE});
    },3000);
  }
  
  const handleQuantityChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    dispatch({type:ADD_TO_CART,id,quantity:+event.target.value});
    dispatch({type:UPDATE_USER});
    setQuantity(+event.target.value);
  };

  return (
    <Card elevation={12} className={classes.root}>
    <CardHeader
      action={
        <IconButton aria-label="settings" onClick={onDelete}>
          <DeleteIcon />
        </IconButton>
      }
      title={book?.title}
      subheader={`by ${authors}`}
      titleTypographyProps={{align:'left'}}
      subheaderTypographyProps={{align:'left'}}
    />
    <CardMedia
      className={classes.media}
      image={book?.imageLinks?.thumbnail}
      title={book?.title}
    />
    <CardContent>
    <Grid item container xs={12}>
            <Grid item xs={12}>
            <Typography className={classes.description} align="left">By {authors}</Typography>
            </Grid>
            <Grid item xs={12}>
            {book?.averageRating && <Typography align="left"><Rating defaultValue={book?.averageRating} readOnly precision={0.1}></Rating></Typography> }
            </Grid>
            <Grid item xs={12}>
            <Typography className={classes.description} align="left">Price:-<del>{((100*price)/(100-discount)).toFixed(2)}</del>&nbsp;<em>{price}</em>&nbsp;INR</Typography>
            </Grid>
            {isCart && <Grid item xs={12}>
            <FormControl fullWidth>
            <InputLabel htmlFor="my-input">Quantity</InputLabel>
            <Input id="my-input" aria-describedby="my-helper-text" type='number' value={quantity} onChange={handleQuantityChange} />
            </FormControl>
            </Grid>}
          </Grid>
    </CardContent>
  </Card>
  )
}

export default CartItem
