import { colors, darkColors } from "../../common/constants";
import { ThemeState } from "../../common/interfaces";
import { TOGGLE_THEME } from "../actions/actions";

const lightState = {
  isDark:false,
  colors:colors
}
const darkState = {
  isDark:true,
  colors:darkColors
}

const initState:ThemeState = darkState;

const toggleTheme = (state:ThemeState, action:any)=>{
  return state.isDark ? lightState : darkState;
}

export default (state = initState, action:any) => {
  switch (action.type) {
    case TOGGLE_THEME: return toggleTheme(state,action);
    default:
      return state;
  }
};