import React from 'react'
import { useSelector } from 'react-redux'
import { StoreState } from '../../common/interfaces'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';

const Message = () => {
  const message = useSelector((store:StoreState)=>store.common.message);
  if(message===null)return<div/>
  return (
    <div>
        <Snackbar open={message!=null} autoHideDuration={2000}>
        <MuiAlert severity={message.severity}>
          {message.message}
        </MuiAlert>
        </Snackbar>
    </div>
  )
}

export default Message
