import { FormState } from "../../containers/Auth";
import * as actionTypes from './actions';
import {Dispatch} from 'redux';
import firebase from 'firebase';
import { UserState } from "../../common/interfaces";
import CryptoJS from 'crypto-js';
import { secret_key } from "../../common/constants";

export const signUpMethod = (data:FormState)=>{
  return (dispatch:Dispatch)=>{
  dispatch({type:actionTypes.TOGGLE_LOADING});
  //=====================================================================
  firebase.auth().createUserWithEmailAndPassword(data.emailId, data.password)
  .then((userCredential) => {
    // Signed in 
    var user = userCredential.user;
    user?.updateProfile({
      displayName:data.username,
    }).then(() => {
    }).catch((error) => {
    });
    const userState:UserState = {id:user?.uid,username:data.username,address:data.address,zipCode:data.zipcode,genres:data.genres,cart:[],liked:[],orders:[]};
    firebase.database().ref('users/' + user?.uid).set(userState);

    // SAVING DATA
    localStorage.setItem('book-store',CryptoJS.AES.encrypt(JSON.stringify(data), secret_key).toString());

    dispatch({type:actionTypes.SET_USER,userState});
    dispatch({type:actionTypes.SET_IS_AUTH, isAuth:true});
  })
  .catch((error) => {
    dispatch({type:actionTypes.SET_MESSAGE,message:{severity:'error',message:error?.message}});
    setTimeout(()=>{
      dispatch({type:actionTypes.CLEAR_MESSAGE});
    },3000);
  });
  dispatch({type:actionTypes.TOGGLE_LOADING});
  }
}

export const loginMethod = (data:FormState)=>{
  return (dispatch:Dispatch)=>{loginStartMethod(data,dispatch)};
}

export const autoLogin = () =>{
  return (dispatch:Dispatch)=>{
    const dataString = localStorage.getItem('book-store');
    if(dataString!==null){
      const bytes  = CryptoJS.AES.decrypt(dataString, secret_key);
      const data = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
      loginStartMethod(data,dispatch);
    }
  }
}

export const signOut = ()=>{
  return (dispatch:Dispatch)=>{
    firebase.auth().signOut().then(() => {
      dispatch({type:actionTypes.CLEAR_ALL});
    }).catch((error) => {
    });
    localStorage.removeItem('book-store');
  }
}


const loginStartMethod = (data:FormState,dispatch:Dispatch)=>{

  dispatch({type:actionTypes.TOGGLE_LOADING});
  firebase.auth().signInWithEmailAndPassword(data.emailId, data.password).then((userCredential) => {
    var userCred = userCredential.user;
    
    // SAVING DATA
    localStorage.setItem('book-store',CryptoJS.AES.encrypt(JSON.stringify(data), secret_key).toString());

    const dbRef = firebase.database().ref();
    dbRef.child("users").child(userCred?.uid||'').get().then((snapshot) => {
    if (snapshot.exists()) {
      dispatch({type:actionTypes.SET_USER,userState:snapshot.val()});
    } else {
      console.log("No data available");
    }
  }).catch((error) => {
    console.error(error);
  });
    dispatch({type:actionTypes.SET_IS_AUTH, isAuth:true});
  })
  .catch((error) => {
    dispatch({type:actionTypes.SET_MESSAGE,message:{severity:'error',message:error?.message}});
    setTimeout(()=>{
      dispatch({type:actionTypes.CLEAR_MESSAGE});
    },3000);
  });
  dispatch({type:actionTypes.TOGGLE_LOADING});
}