import React from 'react'
import { useCallback } from 'react';
import { useDispatch } from 'react-redux'
import { EXPLORE } from '../common/routes';
import BookSlider from '../components/BookSlider/BookSlider'
import DiscoverBooks from '../components/DiscoverBooks/DiscoverBooks'
import * as actions from '../store/actions/index';

const Home = (props:any) => {

  const dispatch = useDispatch();
  
  const goToBook = useCallback((id:string) =>{
    dispatch(actions.getBookAction(id));
    props.history.push(`/bookInfo/${id}`);
  },[]);
  
  const goToExplore = useCallback(()=>{
    props.history.push(EXPLORE);
  },[])

  return (
    <div>
      <BookSlider goToBook={goToBook}></BookSlider>
      <DiscoverBooks goToBook={goToBook} goToExplore={goToExplore}></DiscoverBooks>
    </div>
  )
}

export default Home
