import React from 'react'
import Slider from 'react-slick'
import useStyles from '../../common/CommonStyles';
import { books } from '../../static/BooksData';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft';
import Book1 from '../Book/Book1';
import { useSelector } from 'react-redux';
import { StoreState } from '../../common/interfaces';
import '../../../node_modules/slick-carousel/slick/slick-theme.css'
import '../../../node_modules/slick-carousel/slick/slick.css'

interface Props{
  goToBook: (id: string) => void;
}

const BookSlider:React.FC<Props> = ({goToBook}) => {
  const classes = useStyles();
  const colors = useSelector((state:StoreState)=>state.theme.colors);
  var settings = {
    dots: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 3,
    arrows:true,
    autoplay:true,
    autoplaySpeed:5000,
    slidesToScroll: 1,
    nextArrow:<KeyboardArrowRightIcon/>,
    prevArrow:<KeyboardArrowLeftIcon/>,
    responsive: [
      {
        breakpoint: 1280,
        settings: {
          slidesToShow: 3,
        }
      },
      {
        breakpoint: 1120,
        settings: {
          slidesToShow: 2,
          initialSlide: 1
        }
      },
      {
        breakpoint: 800,
        settings: {
          slidesToShow: 1,
        }
      }
    ]
  };
  return (
    <div className={classes.w98vw}>
      <Slider {...settings}>
        {books.map((item:any,index:number)=><Book1  goToBook={goToBook} key={item.id} book={item} color={colors[index%12]}></Book1>)}
    </Slider>
    </div>
  )
}

export default BookSlider
